.container {
    width: 100%;
    margin: 0 auto;
    padding-left: 20px;
    padding-right: 20px;
  }
  
  @media only screen and (max-width: 768px) {
    .container {
      max-width: 375px;
      padding-left: 20px;
      padding-right: 20px;
    }
  }
  
  @media only screen and (min-width: 768px) and (max-width: 1440px) {
    .container {
      max-width: 768px;
      padding-left: 32px;
      padding-right: 32px;
    }
  }
  
  @media only screen and (min-width: 1440px) {
    .container {
      max-width: 1440px;
      padding-left: 100px;
      padding-right: 100px;
    }
  }