.header-page {
  padding-top: 18px;
  padding-bottom: 36px;
  background-color: var(--primary-bg-color);
}

.header-page-container {
  padding: 0px;
  width: 100%;
}

.header-page-navigation {
  width: 100%;
}

.header-list {
  display: flex;
  gap: 6px; 
  align-items: center;
  width: 100%;
}
.header-first-elements {
  display: flex;
  margin-right: auto;
  align-items: center;
   
}
.header-logotype {
  color: var(--title-black-text-color);
  font-family: FONTSPRING DEMO - Quincy CF Black;
  font-size: 18px;
  font-style: normal;
  font-weight: 900;
  line-height: 20px; 
  letter-spacing: -0.36px;
  display: flex;
  gap: 8px;
  margin-right: auto;
  align-items: center;
  justify-content: center;
}
.header-home-ref {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 30px;
  width: 77px;
  height: 38px;
  color: var(--primary-bg-color);
  background-color: var(--secondary-bg-green-color);
  font-family: Neue Montreal;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px; /* 128.571% */
  }
.header-logotype-icon {
  width: 28px;
  height: 28px;
  flex-shrink: 0;
  background-color: var(--secondary-bg-green-color) ;
  border-radius: 50%;
  fill: var(--primary-bg-color) ;
   padding: 6px; 
}
.header-logotype-icon-card {
  width: 38px;
  height: 38px;
  flex-shrink: 0;
  background-color: var(--secondary-bg-green-color) ;
  border-radius: 50%;
  fill: var(--primary-bg-color) ;
  padding: 10px; 
}
.header-shopping-card-link {
  display: flex;
  align-items: center;
  justify-content: center;
}

.hidden {
  display: none;
}


@media only screen and (max-width: 768px) {
  .container {
    max-width: 375px;
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media only screen and (min-width: 768px) and (max-width: 1440px) {
  .container {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
  .header-list {
    gap: 16px; 
  }
  .header-logotype-icon {
    width: 28px;
    height: 28px;
    padding: 6px; 
  }
  .header-logotype {
    font-size: 24px;
    line-height: 24px; 
    letter-spacing: -0.48px;
    
  }
  .header-home-ref {
    width: 104px;
    height: 48px;
    font-size: 18px;
    line-height: 24px; /* 133.333% */
    }
    .hidden {
      display: block;
      color: var(--title-black-text-color);
      font-family: Neue Montreal;
      font-size: 18px;
      font-style: normal;
      font-weight: 500;
      line-height: 24px; /* 133.333% */
      text-transform: uppercase;
    }
    .header-shopping-card-link {
      display: flex;
      gap: 14px;
      align-items: center;
      justify-content: center;
    }



}

@media only screen and (min-width: 1440px) {
  .container {
    max-width: 1440px;
    padding-left: 100px;
    padding-right: 100px;
  }
  .header-logotype-icon {
    width: 32px;
    height: 32px;
    
  }
  .header-logotype {
    color: var(--title-black-text-color);
    font-family: FONTSPRING DEMO - Quincy CF Black;
    font-size: 20px;
    font-style:normal;
    font-weight: 1200;
    line-height: 20px; 
    letter-spacing: -0.50px;
    display: flex;
    gap: 8px;
    margin-right: auto;
    align-items: center;
    justify-content: center;
  }

  .header-home-ref {
    width: 104px;
    height: 48px;
    font-size: 18px;
    line-height: 24px; /* 133.333% */
    }
    .header-list {
      gap: 24px; 
    }
    .hidden {
      display: block;
      color: var(--title-black-text-color);
      font-family: Neue Montreal;
      font-size: 18px;
      font-style: normal;
      font-weight: 500;
      line-height: 24px; /* 133.333% */
      text-transform: uppercase;
    }
    .header-shopping-card-link {
      display: flex;
      gap: 14px;
      align-items: center;
      justify-content: center;
    }
  
}