html {
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
}

main {
  display: block;
}

h1 {
  margin: .67em 0;
  font-size: 2em;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace, monospace;
  font-size: 1em;
}

a {
  background-color: rgba(0, 0, 0, 0);
}

abbr[title] {
  border-bottom: none;
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

img {
  border-style: none;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, input {
  overflow: visible;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

button::-moz-focus-inner, [type="button"]::-moz-focus-inner, [type="reset"]::-moz-focus-inner, [type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring, [type="button"]:-moz-focusring, [type="reset"]:-moz-focusring, [type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: .35em .75em .625em;
}

legend {
  box-sizing: border-box;
  color: inherit;
  max-width: 100%;
  white-space: normal;
  padding: 0;
  display: table;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type="checkbox"], [type="radio"] {
  box-sizing: border-box;
  padding: 0;
}

[type="number"]::-webkit-inner-spin-button, [type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template, [hidden] {
  display: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

p:last-child {
  margin-bottom: 0;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

:root {
  --primary-bg-color: #e8e8e2;
  --title-black-text-color: #010101;
  --secondary-text-color: rgba(1, 1, 1, .3);
  --title-white-text-color: #e8e8e2;
  --primary-bg-green-color: #586f1f;
  --secondary-bg-green-color: #6d8434;
  --card-bg-color: #fafafa;
  --cancel-background: rgba(1, 1, 1, .07);
  --backdrop-background: rgba(1, 1, 1, .4);
  --modal-text: rgba(1, 1, 1, .7);
}

*, :before, :after {
  box-sizing: border-box;
}

@font-face {
  font-family: Neue Montreal Medium;
  src: url("PPNeueMontreal-Medium.0183d55c.otf");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: Neue Montreal Regular;
  src: url("PPNeueMontreal-Book.a5f3aed6.otf");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: Quincy CF;
  src: url("QuincyCF-Black.4fe7690f.ttf");
  font-weight: normal;
  font-style: normal;
}

html {
  scroll-behavior: smooth;
}

body {
  color: var(--title-black-text-color);
  font-family: Neue Montreal Regular;
  font-size: 12px;
  font-weight: 400;
}

input {
  width: 100%;
}

.is-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media only screen and (max-width: 768px) {
  .container {
    max-width: 375px;
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media only screen and (min-width: 768px) and (max-width: 1440px) {
  .container {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media only screen and (min-width: 1440px) {
  .container {
    max-width: 1440px;
    padding-left: 100px;
    padding-right: 100px;
  }
}

.header-page {
  background-color: var(--primary-bg-color);
  padding-top: 18px;
  padding-bottom: 36px;
}

.header-page-container {
  width: 100%;
  padding: 0;
}

.header-page-navigation {
  width: 100%;
}

.header-list {
  width: 100%;
  align-items: center;
  gap: 6px;
  display: flex;
}

.header-first-elements {
  align-items: center;
  margin-right: auto;
  display: flex;
}

.header-logotype {
  color: var(--title-black-text-color);
  font-family: FONTSPRING DEMO - Quincy CF Black;
  letter-spacing: -.36px;
  justify-content: center;
  align-items: center;
  gap: 8px;
  margin-right: auto;
  font-size: 18px;
  font-style: normal;
  font-weight: 900;
  line-height: 20px;
  display: flex;
}

.header-home-ref {
  width: 77px;
  height: 38px;
  color: var(--primary-bg-color);
  background-color: var(--secondary-bg-green-color);
  border-radius: 30px;
  justify-content: center;
  align-items: center;
  font-family: Neue Montreal;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
  display: flex;
}

.header-logotype-icon {
  width: 28px;
  height: 28px;
  background-color: var(--secondary-bg-green-color);
  fill: var(--primary-bg-color);
  border-radius: 50%;
  flex-shrink: 0;
  padding: 6px;
}

.header-logotype-icon-card {
  width: 38px;
  height: 38px;
  background-color: var(--secondary-bg-green-color);
  fill: var(--primary-bg-color);
  border-radius: 50%;
  flex-shrink: 0;
  padding: 10px;
}

.header-shopping-card-link {
  justify-content: center;
  align-items: center;
  display: flex;
}

.hidden {
  display: none;
}

@media only screen and (max-width: 768px) {
  .container {
    max-width: 375px;
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media only screen and (min-width: 768px) and (max-width: 1440px) {
  .container {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }

  .header-list {
    gap: 16px;
  }

  .header-logotype-icon {
    width: 28px;
    height: 28px;
    padding: 6px;
  }

  .header-logotype {
    letter-spacing: -.48px;
    font-size: 24px;
    line-height: 24px;
  }

  .header-home-ref {
    width: 104px;
    height: 48px;
    font-size: 18px;
    line-height: 24px;
  }

  .hidden {
    color: var(--title-black-text-color);
    text-transform: uppercase;
    font-family: Neue Montreal;
    font-size: 18px;
    font-style: normal;
    font-weight: 500;
    line-height: 24px;
    display: block;
  }

  .header-shopping-card-link {
    justify-content: center;
    align-items: center;
    gap: 14px;
    display: flex;
  }
}

@media only screen and (min-width: 1440px) {
  .container {
    max-width: 1440px;
    padding-left: 100px;
    padding-right: 100px;
  }

  .header-logotype-icon {
    width: 32px;
    height: 32px;
  }

  .header-logotype {
    color: var(--title-black-text-color);
    font-family: FONTSPRING DEMO - Quincy CF Black;
    letter-spacing: -.5px;
    justify-content: center;
    align-items: center;
    gap: 8px;
    margin-right: auto;
    font-size: 20px;
    font-style: normal;
    font-weight: 1200;
    line-height: 20px;
    display: flex;
  }

  .header-home-ref {
    width: 104px;
    height: 48px;
    font-size: 18px;
    line-height: 24px;
  }

  .header-list {
    gap: 24px;
  }

  .hidden {
    color: var(--title-black-text-color);
    text-transform: uppercase;
    font-family: Neue Montreal;
    font-size: 18px;
    font-style: normal;
    font-weight: 500;
    line-height: 24px;
    display: block;
  }

  .header-shopping-card-link {
    justify-content: center;
    align-items: center;
    gap: 14px;
    display: flex;
  }
}



.filters {
  background-color: var(--primary-bg-color);
  padding-top: 37px;
  padding-bottom: 20px;
}

.filters-title {
  color: rgba(1, 1, 1, .3);
  margin-bottom: 8px;
}

.filters-form {
  width: 100%;
  flex-direction: column;
  gap: 12px;
  display: flex;
}

@media only screen and (min-width: 768px) {
  .filters-form {
    flex-direction: row;
    gap: 8px;
  }
}

.filters-search-wrap, .filters-wrap {
  position: relative;
}

.filters-input, .filters-categories, .filters-allTypes {
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid rgba(1, 1, 1, .3);
  border-radius: 30px;
  padding: 14px 20px;
}

@media only screen and (min-width: 768px) {
  .filters-input {
    width: 269px;
  }

  .filters-categories {
    width: 189px;
  }

  .filters-allTypes {
    width: 200px;
  }
}

.filters-allTypes {
  background-color: var(--card-bg-color);
  border-color: rgba(0, 0, 0, 0);
}

.filters-input:focus {
  outline: 1px solid #6d8434;
}

.filters-search-btn {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  align-content: center;
  justify-content: center;
  display: flex;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translate(-50%, -50%);
}

.filters-down-svg {
  fill: var(--primary-bg-color);
  stroke: var(--title-black-text-color);
  transition: all .3s;
  position: absolute;
  top: 50%;
  right: 16px;
  transform: translate(-50%, -50%);
}

.rotate {
  transform: translate(-50%, -50%)rotate(180deg);
}

.filters-categories-list, .filters-allTypes-list {
  z-index: 2;
  width: 100%;
  visibility: hidden;
  opacity: 0;
  background: #fff;
  border-radius: 15px;
  padding: 14px 20px;
  transition: all .3s;
  position: absolute;
  top: 56px;
  left: 0;
  box-shadow: 0 4px 11.2125px rgba(0, 0, 0, .04), 0 20px 69px rgba(0, 0, 0, .07);
}

.list-active {
  visibility: visible;
  opacity: 1;
}

.filters-categories-item, .filters-allTypes-item {
  color: rgba(1, 1, 1, .5);
  margin-bottom: 9px;
  font-size: 14px;
}



