.backdrop {
    /* position: fixed; */
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(1, 1, 1, 0.40);
}

.modal-product {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    width: 335px;
    height: 535px;
    border-radius: 20px;
     background: var(--card-bg-color);
     padding: 40px 20px;
}

@media  screen and (min-width: 768px) {
    .modal-product {
        width: 600px;
       height: 380px;
       padding: 40px 40px;
    }
}

.modal-btn-close {
    padding: 0;
    position: absolute;
    top: 14px;
    right: 14px;
    width: 22px;
height: 22px;
        
    display: flex;
    justify-content: center;
    align-items: center;

    border: none;
    background: var(--card-bg-color);
}


@media  screen and (min-width: 768px) {
    .modal-btn-close { 
        top: 24px;
        right: 24px;
        width: 28px;
height: 28px;
    } 
}

.modal-svg-close {
}

.modal-product-info {
    display: block;
}

@media  screen and (min-width: 768px) {
    .modal-product-info {
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-between;
    }
}

.temporaneo {
    /* width: 180px;
    height: 180px; */
    /* padding: 26px 40px; */
    width: 295px;
height: 212px;
    flex-shrink: 0;
    border-radius: 10px;
    background:var(--primary-bg-color);
}



@media  screen and (min-width: 768px) {
    .temporaneo {
        /* width: 180px;
        height: 180px; */
        /* padding: 26px 40px; */
        width: 240px;
        height: 212px;
        flex-shrink: 0;
        border-radius: 10px;
        background:var(--primary-bg-color);
    }
}

.modal-product-description {
    display: flex;
    flex-direction: column;
    padding-top: 20px;
}

@media  screen and (min-width: 768px) {
    .modal-product-description {
        display: inline-flex;
        align-items: flex-start;
        padding: 0;
    }
}

.modal-title {
    font-family: "Neue Montreal Medium";
    font-size: 18px;
    font-style: normal;
    font-weight: 500;
    line-height: 1.1em;
    padding-bottom: 10px;
}


@media  screen and (min-width: 768px) {
    .modal-title {
        font-size: 20px;
        line-height: 1.2em;
    }
}

.modal-product-list {
    display: flex;
    width: 295px;
    flex-direction: row;
    align-items: flex-start;
    flex-wrap: wrap;
    gap: 9px;
   
}


@media  screen and (min-width: 768px) {
    .modal-product-list {
        width: 246px;
    }
}

.modal-product-item {
    display: flex;
align-items: flex-start;
flex-direction: row;
gap: 4px;
}

.modal-product-caption {
    font-family: "Neue Montreal Regular";  
    color: rgba(1, 1, 1, 0.30);
font-size: 12px;
font-style: normal;
font-weight: 400;
line-height: 1.6em;
display: flex;
    align-items: flex-end;
}

.modal-product-content {
    font-family: "Neue Montreal Regular";
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 1.6em;
    display: flex;
    align-items: flex-end;
}

.modal-product-text {
font-size: 14px;
font-style: normal;
font-weight: 400;
line-height: 1.28em;
color: rgba(1, 1, 1, 0.70);
font-family: "Neue Montreal Regular";
    width: 295px;
padding-top: 20px;
}

@media  screen and (min-width: 768px) {
    .modal-product-text {
    font-size: 16px;
        width: 256px;
    line-height: 1.25em;
    padding-top: 24px;
    }
}

.modal-product-wrapper-price {
    display: flex;
    justify-content: space-between;
    padding-top: 40px;
}

@media  screen and (min-width: 768px) {
    .modal-product-wrapper-price {
       padding-top: 48px;
    } 
}

.modal-product-price {
    font-family: "Neue Montreal Medium";
font-size: 18px;
font-style: normal;
font-weight: 500;
    line-height: 1.1em;
    display: flex;
    align-items: flex-end;
}


@media  screen and (min-width: 768px) {
    .modal-product-price {
    font-size: 20px;
        line-height: 1.2em;
    }  
}

.modal-product-btn-price {
    color: var(--title-white-text-color);
    font-family: "Neue Montreal Medium";
font-size: 14px;
font-style: normal;
font-weight: 500;
line-height: 1.28em;
border-radius: 30px;
background: var(--secondary-bg-green-color);
display: flex;
padding: 10px 24px;
justify-content: center;
align-items: center;
gap: 10px;
border: none;
}


@media  screen and (min-width: 768px) {
    .modal-product-btn-price {
    font-size: 16px;
    line-height: 1.25em;
    padding: 10px 28px;
    }
}

.modal-product-btn-price:hover,
.modal-product-btn-price:focus {
  background: var(--primary-bg-green-color);
}
.modal-btn-svg {
}
.modal-icon-svg {
    fill: var(--primary-bg-color);
}

